<template>
  <v-container fluid>
    <v-data-iterator
      :items="rooms?rooms:[]"
      :total-items="rooms?rooms.length:0"
      hide-actions
    >
      <template v-slot:item="props">
        <v-card
          flat
          :width="roomCardAutoWidth"
          v-ripple
          fluid
          :color="state2Color(props.item.roomState)"
          class="rooms mr-2 mt-2"
          hover
          @click.stop="clickRoom(props.item)"
        >
          <v-card-title>
            <div>
              <div class="">{{props.item.roomName}}</div>
              <div class="">{{props.item.roomTypeName}}</div>
              <div class="">{{ getText('roomState',props.item.roomState) }}</div>
              <span></span>
            </div>
          </v-card-title>
        </v-card>
      </template>
    </v-data-iterator>
    <ViewOrder :show="viewOrder.show" :order-no="currRoom?currRoom.orderNo:null" 
      @view-order-close="viewOrder.show = false" />
  </v-container>
</template>

<script>
import { mapState } from "vuex";
import { mapActions } from "vuex";
import { mapGetters } from "vuex";
import ViewOrder from '../components/order/ViewOrder.vue'
export default {
  name: "RoomState",
  components: {
    ViewOrder
  },
  data() {
    return {
      currRoom: null,
      viewOrder: {
        show: false
      },
    };
  },
  computed: {
    roomCardAutoWidth() {
      switch (this.$vuetify.breakpoint.name) {
        case "xs":
          return "100px";
        case "sm":
          return "110px";
        case "md":
          return "120px";
        case "lg":
          return "135px";
        case "xl":
          return "150px";
      }
    },
    ...mapState({
      rooms: state => state.room.rooms,
      roomState: state => state.dictionary.roomState,
      currHotel: state => state.user.currHotel
    }),
    ...mapGetters([
      'getText'
    ])
  },
  mounted() {
    this.rooms && this.rooms.length <=0 ?'':this.getRooms();
    this.getDicByType('roomState');
    this.getDicByType('channel');
    this.getDicByType('pay_type');
    
  },
  methods: {
    clickRoom(currRoom) {
      this.currRoom = currRoom;
      console.log(currRoom.orderNo);
      if (currRoom.orderNo) {
        this.viewOrder.show = true;
      } 
    },
    state2Color(state) {
      switch (state) {
        case 'vacant':
          return 'rgb(208, 243, 208)';
        case 'dirty':
          return 'rgb(247, 245, 201)';
        case 'rented' || 'rented_hour':
          return 'rgb(165, 216, 250)';
        case 'repair':
          return '#e8eaec';
      
        default:
          return 'rgb(225, 226, 225)';
      }
    },
    ...mapActions({
      getRooms: "getRooms",
      getDicByType: "getDicByType"
    })
  }
};
</script>

<style>
.rooms {
  float: left;
}
.room {
  background-color: aquamarine;
}
</style>
